/*intro page*/

.container-video h1 {
    text-align: center;
}

.container-video h2 {
    text-align: center;
}

.container-video h3 {
    text-align: center;
}

.rh5v-Overlay_component {
    background-color: rgba(0, 48, 164, 0.2) !important;
}

.rowintropage {
    margin-top: 120px;
}

.language-box {
    text-align: center;
}

.language-box a {
    transition: all 0.4s
}

.language-box a:hover {
    color: white;
}

#particles-js {
    height: 100px;
    background: blue;
}

.container-title {
    position: relative;
    z-index: 2;
}

.container-title h1, .container-title h2 {
    text-align: center;
}

.container-title span.name {
    color: rgb(252, 69, 130);
}

.container-button {
    margin-top: 50px;
    text-align: center;
}

.btnStart {
    background: #ff000000;
    border: 2px solid white;
    border-radius: 40px;
    font-weight: lighter;
}

.btnStart:hover {
    background: #ffffff14;
}

.list-social {
    opacity: 0.8;
}

.list-social a {
    transition: all 0.4s;
    color: white;
}

.list-social a:hover {
    opacity: 0.4;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    height: 45px !important;
    padding-top: 0px !important;
}

.sticky ul {
    padding-top: 5px !important;
}

.navbar a {
    color: white;
    transition: all 0.4s;
}

.navbar a:hover {
    color: rgb(101, 124, 156);
    font-weight: 500;
}

.carousel-slider {
    border-radius: 20px;
}

.introtitlepage {
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}

.colizqpi, .colderepi {
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
    opacity: 0;
    transition: all 0.4s;
}

.contactZone input {
    color: white;
}

.slides li img {
    border-radius: 5px !important;
}

.slider .slides li {
    border-radius: 5px !important;
}

.slider .slides {
    border-radius: 5px !important;
}

.caption h3 {
    font-size: 35px;
}

.caption {
    margin-top: 25px;
    text-shadow: 2px 2px 10px black;
    border-radius: 20px;
}

.caption a {
    text-align: center;
}

.caption h3 {
    border-bottom: #14a6bf solid 4px;
    display: -webkit-inline-box;
}

.slider .indicators .indicator-item.active {
    background-color: #14a6bf;
}

ul.slides {
    height: 460px !important;
}

.slider {
    height: 500px !important;
}

.slider .slides li img {
    -webkit-filter: blur(2px);
            filter: blur(2px);
    transition: all 1s;
}

.btn:focus, .btn-large:focus, .btn-small:focus, .btn-floating:focus {
    background-color: #14a6bf !important;
}

